.add-product-container {
  max-width: 1100px;
  margin: 30px auto;
  padding: 20px;
}

.add-product-container h1 {
  color: #4CAF50;
  margin-bottom: 25px;
  font-size: 24px;
  font-weight: 500;
}

form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}

form > div {
  margin-bottom: 15px;
}

/* Make certain fields span full width */
form > div:nth-child(5),  /* Mô tả */
form > div:nth-child(10) /* Hình ảnh */ {
  grid-column: span 2;
}

label {
  display: block;
  margin-bottom: 8px;
  font-weight: 500;
  color: #333;
  font-size: 14px;
}

input[type="text"],
input[type="number"],
textarea,
select,
input[type="file"] {
  width: 100%;
  padding: 12px;
  border: 1px solid #e1e1e1;
  border-radius: 6px;
  background-color: #f6f9fc;
  font-size: 16px;
  box-sizing: border-box;
}

input::placeholder,
textarea::placeholder {
  color: #aaa;
}

textarea {
  min-height: 100px;
  resize: vertical;
}

button[type="submit"] {
  grid-column: 1 / -1;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 12px 24px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s;
  width: fit-content;
  margin: 20px auto 0;
}

button[type="submit"]:hover {
  background-color: #45a049;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 10px 0 0;
}

li {
  padding: 5px 0;
  font-size: 14px;
  color: #666;
}

/* Style for file input */
input[type="file"] {
  padding: 10px;
}

/* Make responsive */
@media (max-width: 768px) {
  form {
    grid-template-columns: 1fr;
  }
  
  form > div:nth-child(5),
  form > div:nth-child(10) {
    grid-column: 1;
  }
}